.marquee-wrapper {
  text-align: center;
}

.marquee-wrapper .container {
  overflow: hidden;
  max-width: 100%;
}

.marquee-inner span {
  float: left;
  width: 50%;
}

.marquee-wrapper .marquee-block {
  --total-marquee-items: 7;
  height: 365px;
  width: calc(323px * (var(--total-marquee-items)));
  overflow: hidden;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  position: relative;
  margin: 10px auto;
}
.moreitems{
  --total-marquee-items: 12;
}
.marquee-inner {
  display: -webkit-block;
  display: -moz-block;
  display: -ms-block;
  display: block;
  width: 200%;
  position: absolute;
}

.marquee-inner.to-left {
  -webkit-animation: marqueeLeft 20s linear infinite;
  -moz-animation: marqueeLeft 20s linear infinite;
  -o-animation: marqueeLeft 20s linear infinite;
  -ms-animation: marqueeLeft 20s linear infinite;
  animation: marqueeLeft 20s linear infinite;
}

.marquee-inner.to-right {
  -webkit-animation: marqueeRight 20s linear infinite;
  -moz-animation: marqueeRight 20s linear infinite;
  -o-animation: marqueeRight 20s linear infinite;
  -ms-animation: marqueeRight 20s linear infinite;
  animation: marqueeRight 20s linear infinite;
}
.marquee-item img{
  width: 350px;
  height: 350px;

}
.marquee-item {
  width: 350px;
  height: auto;
  display: -webkit-inline-box;
  display: -moz-inline-box;
  display: -ms-inline-flexbox;
  display: inline-block;
  margin: 0 5px;
  /* float: left; */
  -webkit-transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  -ms-transition: all 0.2s ease-out;
  transition: all 0.3s ease-out;
  
  padding: 8px;
}
@-moz-keyframes marqueeLeft {
  0% {
    -webkit-left: 0;
    -moz-left: 0;
    -o-left: 0;
    -ms-left: 0;
    left: 0;
  }
  100% {
    -webkit-left: -100%;
    -moz-left: -100%;
    -o-left: -100%;
    -ms-left: -100%;
    left: -100%;
  }
}
