.flipcard{
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: relative;
  align-items: center;
  border-radius: 0.35em;
  box-shadow: 0 0 5px 5px rgba(0,0,0,0.5);
  
  color: white;
  cursor: pointer;
  transform-style: preserve-3d;
  transition: 300ms;
  transform: perspective(1000px) rotateY(var(--rotate-y, 0))
  translateY(var(--translate-y, 0));
  height: 500px;
  width: 400px;
}
.flipcard:hover{
  --translate-y: -2px;
  box-shadow: 0 0 5px 2px rgba(0,0,0,0.5);
opacity: 1;
}
.flipcard.flip{
  --rotate-y: 180deg;
}
.flipcard .front{
  left: 0;
